export default "import { OverlayConfig } from \"@angular/cdk/overlay\";\nimport { Component } from \"@angular/core\";\nimport { FormControl } from \"@angular/forms\";\nimport { OVERLAY_WITH_POPUP_STYLES_CLASS } from \"@nova-ui/bits\";\n\n@Component({\n    selector: \"nui-combobox-v2-overlay-config-example\",\n    templateUrl: \"./combobox-v2-overlay-config.example.component.html\",\n    host: { class: \"combobox-container\" },\n})\nexport class ComboboxV2OverlayConfigExampleComponent {\n    public items = Array.from({ length: 100 }).map((_, i) => $localize `Item ${i}`);\n\n    public overlayConfig: OverlayConfig = {\n        width: 300,\n        height: 200,\n        panelClass: [OVERLAY_WITH_POPUP_STYLES_CLASS, \"overlay-config-demo-custom-class\"],\n    };\n\n    public comboboxControl = new FormControl();\n}\n";